.no-webp .inicio{
    background-image:url(../img/header.jpg);
    
}
.webp .inicio{
    background-image: url(../img/header.webp);
}

.header{
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio {
        background-position: center;
        background-size: cover;

        @include tablet{
            min-height: 70rem;
            
        }
        .contenido-header{
            @include tablet{
                min-height: 70rem;
            }
        }

    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    .logo{
        color: #fff;
        font-size: 3rem;
    }

    a{
        &:hover{
            color: #21c421;
        }
    }

    h1{
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 2;
    }
}






